/* Header | Hero
   ========================================================================= */

/* Styles for a hero header (big intro section) */
.hero {

  /* By default, take up 15% of viewport */
  @include height-fallback(15);

  /* Set background image to be fixed and cover element */
  background-attachment: fixed;
  background-size: cover;
}
